.hypnotic {
	width: 25px;
	height: 24px;
	display: grid;
	background: linear-gradient(to bottom left ,#0000 calc(50% - 1.1px),#474bff 0 calc(50% + 1.1px),#0000 0) right/50% 100%,
	linear-gradient(to bottom right,#0000 calc(50% - 1.1px),#474bff 0 calc(50% + 1.1px),#0000 0) left /50% 100%,
	linear-gradient(#474bff 0 0) bottom/100% 2.2px;
	background-repeat: no-repeat;
	transform-origin: 50% 66%;
	animation: hypnotic-k788tl 4s infinite linear;
}

.hypnotic::before,
.hypnotic::after {
	content: "";
	grid-area: 1/1;
	background: inherit;
	transform-origin: inherit;
	animation: inherit;
}

.hypnotic::after {
	animation-duration: 2s;
}

@keyframes hypnotic-k788tl {
	100% {
		transform: rotate(1turn);
	}
}